main {
  position: relative;
  min-height: calc(100vh - 300.994px);
  display: flex;
  justify-content: center;
  align-items: center;
}
.block {
  background-color: white;
  border-radius: 50%;
  position: absolute;
  width: 150px;
  height: 150px;
  padding: 1.8rem;
  cursor: pointer;
}
.paper {
  top: -35%;
  left: 5%;
  border: 20px solid hsl(230, 89%, 62%);
}
.rock {
  border: 20px solid hsl(349, 71%, 52%);
  top: 50%;
  left: 30%;
}
.sicssors {
  border: 20px solid hsl(39, 89%, 49%);
  top: -35%;
  left: 55%;
}
.traingle {
  max-width: 250px;
}
.traingle-block {
  background-image: url("./images/bg-triangle.svg");
  width: 100%;
  height: 200px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  position: relative;
}

@media screen and (min-width: 500px) {
  main {
    max-width: 500px;
    margin: 0 auto;
  }
  .paper {
    left: 10%;
  }
  .rock {
    left: 35%;
  }
  .sicssors {
    left: 60%;
  }
}

@media screen and (max-height: 578px) {
  main {
    min-height: 60vh;
  }
}

@media screen and (max-height: 523px) {
  .block {
    width: 100px;
    height: 100px;
    padding: 1rem;
  }
  .paper {
    left: 20%;
    top: -20%;
  }
  .sicssors {
    top: -20%;
  }
  .rock {
    left: 40%;
  }
}
